test('Property sale', async t => {
    const inputLandlord1 = {name:'TestLL', money: 500, properties: []}
    const inputLandlord2 = {name:'TestR', money: 900, months: ['Jan'], property: ["House"]}
    const inputProperty = {name:'TestP', cost: 10}
 
    const landlordCreation1 = (await request(app)
        .post('/landlord/add')
        .send(inputLandlord1))
        .body

    const landlordCreation2 = (await request(app)
        .post('/landlord/add')
        .send(inputLandlord2))
        .body

    const propertyCreation = (await request(app)
        .post('/property/add')
        .send(inputProperty))
        .body

    const input = {landlordID1: landlordCreation1.landlordID, landlordID2: landlordCreation2.landlordID, propertyID: propertyCreation.propertyID}

    const res = await request(app)
        .post('/landlord/sell-property')
        .send(input)

    t.is(res.status, 200)
})